@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');

.maincontainer-home{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.maincontainer-home .subcontainer{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}


.maincontainer-home .subcontainer.first{
    padding-top: 65px;
    background-image: url("../assets/spacebg.jpg");
    background-size: 2000px;
    display: flex;
    flex-direction: column;
    
}
.subcontainer.first .content1{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFF01F;
    font-family: 'Lora', serif;
    font-size: 60pt;
    font-weight: 900;
}
.subcontainer.first .subcontent1{
    color: #ffffff;
    font-weight: 400;
    margin-top: 20px;
}
.subcontainer .box{
    width: 100px;
    height: 100px;
    background-color: aqua;
}